<template>
	<div class="guide-container">
		<div class="guide-container__bar">
			<guideList :listInfo="NAV_LIST" @infoHandler="linfoHandler"></guideList>
		</div>
		<div class="guide-container__main">
			<Transition name="fade">
				<guideMain :listpProps="listpProps"></guideMain>
			</Transition>
		</div>
	</div>
</template>
<script setup>
import guideList from "@/pages/guide/components/guideList";
import {NAV_LIST} from "@/data/guide";
import guideMain from "@/pages/guide/components/guideMain";
import {stepList} from "@/data/guide";
import {computed} from "vue";
import {useCheckMobile} from "@/store/isMobile";
import {nuxtTo} from "@/utils/tools";

let listpProps = ref(stepList(0));
const linfoHandler = (id) => {
	let res = stepList(id);
	listpProps.value = res;
};
const isMobile = computed(()=>{
	return useCheckMobile().getItem()
})
onMounted(()=>{
	watchEffect(()=>{
		if (isMobile.value){
			nuxtTo('/guide/mob')
		}
	})
})
</script>
<style scoped lang="scss" src="./index.scss"></style>
